  height: 80px;
}
.header-wrap {
  height: 100%;
  padding: 0 100px;
}
.header-wrap .nav {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-wrap .nav .logo > a {
  font-size: 32px;
  font-weight: bold;
  color: purple;
  line-height: 80px;
}
.header-wrap .nav > ul {
  display: flex;
  gap: 24px;
}
.header .aside {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 300px;
  z-index: 9997;
  background-color: #fff;
}
.header .aside-top {
  display: flex;
  justify-content: space-between;
  padding: 0 12px;
}
.header .aside-top .logo > a {
  font-size: 32px;
  font-weight: bold;
  color: purple;
  line-height: 80px;
}
.header .aside-mid > ul > li > a {
  display: inline-block;
  width: 100%;
  padding: 24px 12px;
}

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 80px;
}
.footer-wrap {
  height: 100%;
}
.footer-wrap > ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}
.footer-wrap > ul > li.action > a {
  color: blue;
}
.footer-wrap > ul > li > a {
  font-weight: 700;
  font-size: 20px;
}

button {
  padding: 10px;
